.main-nav {
  position: relative;

  overflow: hidden;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  padding-top: 100px;

  list-style: none;
  transition: .5s;
}

.main-nav--closed .main-nav__list {
  margin-right: -100%;
}

.main-nav--opened .main-nav__list {
  margin-right: 0;
}

.main-nav__item {
  font-family: @font-family-secondary;
  font-size: 20px;
  text-align: right;
  color: #606060;
  text-transform: uppercase;

  &:active {
    color: #000000;
  }
}

.main-nav__item:not(:last-child) {
  margin-bottom: 30px;
}

.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;

  width: 38px;
  height: 27px;

  background-color: transparent;
  border: none;

  &::before,
  &::after {
    content: '';

    position: absolute;
    left: 0;
    top: 50%;

    width: 100%;
    height: 4px;
    margin-top: -2px;

    background-color: #bebebe;
    border-radius: 2px;
    transition: transform .1s .1s,
      box-shadow .1s;
  }
}

.main-nav--closed .main-nav__toggle {
  &::before,
  &::after {
    box-shadow: 0 -12px 0 #bebebe,
    0 12px 0 #bebebe;
    transition: transform .1s,
      box-shadow .1s .1s;
  }
}

.main-nav--opened .main-nav__toggle {
  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }

  &::before,
  &::after {
    box-shadow: 0 0 0 #bebebe,
    0 0 0 #bebebe;
  }
}
