.latter-compositions {
  .wrapper;

  width: 345px;
  margin: 0 auto;

  padding-top: 50px;
  padding-bottom: 206px;
}

.latter-compositions__title {
  margin-bottom: 18px;
}

.latter-compositions__title::before {
  width: 282px;
  margin-left: -141px;
}

.latter-compositions__description {
  margin: 0;
  margin-bottom: 27px;

  font-size: 10px;
  line-height: 14px;
  text-align: center;
  color: #7a8083;
  letter-spacing: .118em;
}

.latter-compositions__playlist {
  margin: 0 auto;
  padding: 0;
  width: 100%;

  list-style: none;
}

.latter-compositions__audio-item {
  padding-top: 16px;

  background-color: #f5f6f6;

  &:not(:last-child) {
    margin-bottom: 23px;
  }
}

.latter-compositions__wrapper {
  width: 100%;
  padding-bottom: 17px;
  padding-left: 16px;
  padding-right: 20px;
}

.latter-compositions__audio-year {
  width: 181px;
  margin: 0;
  padding-bottom: 5px;

  font-size: 10px;
  font-weight: 700;
  color: rgba(0, 0, 0, .65);

  border-bottom: 1px solid #000000;
}

.latter-compositions__audio-title {
  margin: 0;
  margin-bottom: 16px;
  padding-top: 3px;

  font-size: 10px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: .018em;
}

.latter-compositions__audio-annotation {
  margin: 0;

  font-size: 14px;
}

.latter-compositions__audio-description {
  margin: 0;
  margin-bottom: 32px;
  padding-top: 11px;

  font-family: "Myriad Pro", "Arial", sans-serif;
  font-size: 10px;
  line-height: 14px;
  letter-spacing: .018em;
  color: #aaaaaa;
}

.latter-compositions__link {
  position: relative;

  display: inline-flex;
  height: 26px;
  padding: 0 16px 0 9px;
  margin-right: 10px;
  align-items: center;
  justify-content: center;

  font-size: 10px;
  font-weight: 700;
  color: #000000;

  border: 1px solid rgba(98, 98, 98, 0.44);

  &::after {
    content: "";

    position: absolute;
    right: 6px;
    top: 50%;

    width: 5px;
    height: 5px;
    margin-top: -2px;

    border-right: 1px solid #000000;
    border-bottom: 1px solid #000000;
    transform: rotate(-45deg);
  }
}

.latter-compositions__player {
  position: relative;

  width: 100%;
  height: 194px;

  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: cover;

  &--indian-summer {
    background-image: url('../img/indian-summer-mobile@1x.jpg');

    @media @retina {
      background-image: url('../img/indian-summer-mobile@2x.jpg');
    }
  }

  &--indian-summer-remix {
    background-image: url('../img/indian-summer-remix-mobile@1x.jpg');

    @media @retina {
      background-image: url('../img/indian-summer-remix-mobile@2x.jpg');
    }
  }

  &--chill-nation {
    background-image: url('../img/chill-nation-mobile@1x.jpg');

    @media @retina {
      background-image: url('../img/chill-nation-mobile@2x.jpg');
    }
  }
}


