.discography {
  .wrapper;

  padding-bottom: 206px;

  background-image: url('../img/bg-discography@1x.jpg');
  background-repeat: no-repeat;
  background-position: bottom center;

  @media @retina {
    background-image: url('../img/bg-discography@2x.jpg');
    background-size: 920px 362px;
  }
}

.discography__title {
  margin-bottom: 15px;
}

.discography__title svg {
  display: block;
  margin: 0 auto;
}

.discography__title text {
  text-anchor: middle;
  fill: url(#pattern);
}

.discography__description {
  margin: 0;
  margin-bottom: 48px;

  font-size: 10px;
  line-height: 14px;
  text-align: center;
  color: #7a8083;
  letter-spacing: .118em;
}

.discography__list {
  margin: 0;
  padding: 0;

  list-style: none;
}

.discography__item:not(:last-child) {
  margin-bottom: 49px;
}

.discography__year {
  margin: 0;
  padding-bottom: 5px;

  font-size: 10px;
  font-weight: 700;
  color: rgba(0, 0, 0, .65);

  border-bottom: 1px solid #000000;
}

.discography__item-title {
  margin: 0;
  margin-bottom: 16px;
  padding-top: 3px;

  font-size: 10px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: .018em;
}

.discography__item-annotation {
  margin: 0;

  font-size: 14px;
}

.discography__item-description {
  margin: 0;
  margin-bottom: 32px;
  padding-top: 11px;

  font-family: "Myriad Pro", "Arial", sans-serif;
  font-size: 10px;
  line-height: 14px;
  letter-spacing: .018em;
  color: #aaaaaa;
}

.discography__item-link {
  display: flex;
  margin: 0 auto;
  width: 90px;
  height: 26px;
  align-items: center;
  justify-content: center;

  font-size: 10px;
  font-weight: 700;
  color: #000000;
  text-transform: uppercase;

  background-size: cover;

  &--hell-and-silence {
    color: #ffffff;

    background-image: url('../img/hell-and-silence.png');
  }

  &--night-visions {
    background-image: url('../img/night-visions.png');
  }

  &--smoke-mirrors {
    background-image: url('../img/smoke-mirrors.png');
  }
}
