.wrapper() {
  min-width: 345px;
  padding-left: 23px;
  padding-right: 23px;
  margin: 0 auto;

  @media (min-width: @tablet-width) {
    min-width: 1500px;
    padding-left: 16.7%;
    padding-right: 13.3%;
  }

  @media (min-width: @desktop-width) {
    width: 1800px;
    padding-left: 300px;
    padding-right: 300px;
  }
}
