.section-title {
  position: relative;

  margin: 0;

  font-family: @font-family-secondary;
  text-transform: uppercase;
  text-align: center;
  font-size: 23px;
  font-weight: 400;
  letter-spacing: .118em;
  z-index: 1;

  &::before {
    content: '';

    position: absolute;
    left: 50%;
    top: 50%;

    width: 230px;
    height: 1px;
    margin-left: -115px;

    background-color: #000000;
  }
}
