.concert-tours {
  .wrapper;

  padding-top: 45px;
  padding-bottom: 22px;

  background-image: url('../img/bg-concert-tours@1x.jpg');
  background-repeat: no-repeat;
  background-position: bottom left 85%;
}

.concert-tours__title {
  margin-bottom: 15px;
}

.concert-tours__title a {
  background: linear-gradient(180deg, #779da6 10%, #98b5ae 90%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.concert-tours__description {
  min-height: 100px;
  margin: 0;
  margin-bottom: 100px;

  font-size: 10px;
  line-height: 14px;
  text-align: center;
  color: #7a8083;
  letter-spacing: .118em;
}

.concert-tours__container {
  width: 61%;
}

.concert-tours__date {
  margin: 0;
  padding-bottom: 5px;

  font-size: 10px;
  font-weight: 700;
  color: rgba(0, 0, 0, .65);

  border-bottom: 1px solid #000000;
}

.concert-tours__name {
  margin: 0;
  margin-bottom: 35px;
  padding-top: 3px;

  font-size: 10px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: .018em;
}

.concert-tours__annotation {
  margin: 0;

  font-size: 14px;
}

.concert-tours__tour-description {
  margin: 0;
  margin-bottom: 30px;
  padding-top: 11px;

  font-family: "Myriad Pro", "Arial", sans-serif;
  font-size: 9px;
  line-height: 12px;
  letter-spacing: .018em;
  color: #7a8083;
}

.concert-tours__link {
  position: relative;

  display: inline-flex;
  height: 26px;
  padding: 0 31px 0 24px;
  align-items: center;
  justify-content: center;

  font-size: 10px;
  font-weight: 700;
  color: #000000;

  border: 1px solid rgba(98, 98, 98, 0.44);

  &::after {
    content: "";

    position: absolute;
    right: 21px;
    top: 50%;

    width: 5px;
    height: 5px;
    margin-top: -2px;

    border-right: 1px solid #000000;
    border-bottom: 1px solid #000000;
    transform: rotate(-45deg);
  }
}
