.player-control {
  position: absolute;
  bottom: 12px;
  left: 50%;

  display: flex;
  align-items: center;
  width: 252px;
  height: 15px;
  margin-left: -129px;

  font-weight: 300;
  font-size: 7px;
  line-height: 7px;
  color: #ffffff;
}

.player-control__toggle {
  position: relative;

  width: 14px;
  height: 100%;
  margin-right: 8px;

  border: none;
  background-color: transparent;

  &:before {
    content: '';

    position: absolute;
    left: 5px;
    top: 50%;

    width: 0;
    height: 0;
    margin-top: -5px;

    border: 5px solid transparent;
    border-left: 7px solid #ffffff;
  }

  &--playing:before {
    width: 2px;
    height: 8px;
    margin-top: -4px;

    border: none;
    background-color: #ffffff;
    box-shadow: 4px 0 0 #ffffff;
  }
}

.player-control__bar {
  position: relative;

  width: 180px;
  height: 2px;
  margin-right: 6px;

  background-color: #6f6f6f;
}

.player-control__progress-bar {
  height: 100%;
  width: 0;

  background-color: #ffffff;
}

.player-control__pin {
  position: absolute;
  top: -2px;
  left: 0;

  display: none;
  width: 6px;
  height: 6px;

  background-color: #ffffff;
  border-radius: 50%;
}

.player-control__toggle--current + .player-control__bar .player-control__pin {
  display: block;
}

.player-control__time {
  margin: 0;
  flex-grow: 1;

  text-align: right;
  color: #ffffff;
}

.player-control__total-time {
  color: #898989;
}
