.social {
  display: inline-flex;
  align-items: center;
  padding: 0;
  padding-bottom: 5px;
  vertical-align: bottom;

  font-family: "Myriad Pro", "Arial", sans-serif;
  font-size: 8px;
  line-height: 8px;
  list-style: none;
}

.social__item {
  position: relative;

  padding-left: 15px;

  &:not(:last-child) {
    margin-right: 10px;
  }

  &:first-child {
    padding-left: 12px;
  }
}

.social__link {
  display: flex;
  align-items: center;
}

.social__link-icon {
  position: absolute;
  left: 0;
  top: -1px;

  margin-right: 3px;

  &--listened {
    top: -2px;
  }
}
